import React  from 'react'
import PropTypes from 'prop-types'
import Transition from 'react-transition-group/Transition'
import './BounseInLeft.css'


const BounseInLeft = ({ in: inProp, children, duration}) => (
  <Transition in={inProp} timeout={duration} unmountOnExit={true}>
    {(status) => {
      return (
        <div
          className={`BounseInLeft BounseInLeft--${status}`}>
          {children}
        </div>
      )}
    }
  </Transition>
)

BounseInLeft.propTypes = {
  in: PropTypes.bool.isRequired,
  duration: PropTypes.number,
  children: PropTypes.node
}

BounseInLeft.defaultProps = {
  duration: 500
}


export default BounseInLeft;



