import React  from 'react'
import PropTypes from 'prop-types'
import Transition from 'react-transition-group/Transition'
import './BounseInDown.css'


const BounseInDown = ({ in: inProp, children, duration}) => (
  <Transition in={inProp} timeout={duration} unmountOnExit={true}>
    {(status) => {
      return (
        <div
          className={`BounseInDown BounseInDown--${status}`}>
          {children}
        </div>
      )}
    }
  </Transition>
)

BounseInDown.propTypes = {
  in: PropTypes.bool.isRequired,
  duration: PropTypes.number,
  children: PropTypes.node
}

BounseInDown.defaultProps = {
  duration: 500
}


export default BounseInDown;



