import React  from 'react'
import PropTypes from 'prop-types'
import Transition from 'react-transition-group/Transition'
import './BounseInRight.css'


const BounseInRight = ({ in: inProp, children, duration}) => (
  <Transition in={inProp} timeout={duration} unmountOnExit={true}>
    {(status) => {
      return (
        <div
          className={`BounseInRight BounseInRight--${status}`}>
          {children}
        </div>
      )}
    }
  </Transition>
)

BounseInRight.propTypes = {
  in: PropTypes.bool.isRequired,
  duration: PropTypes.number,
  children: PropTypes.node
}

BounseInRight.defaultProps = {
  duration: 500
}


export default BounseInRight;



